	ngOnInit() {

    this.registerForm = this.createFormGroup();
  }

  createFormGroup(): FormGroup{

    return new FormGroup ({

      given_name: new FormControl(null,[Validators.required, Validators.minLength(2)]),
     family_name: new FormControl(null,[Validators.required, Validators.minLength(2)]),
      username: new FormControl(null,Validators.required),
      password: new FormControl(null,[Validators.required, Validators.minLength(6)]),
      cnfpass: new FormControl(null,this.passValidator),
     // secretkey: new FormControl(null,this.secretkeyValidator),
      roles: new FormControl([]),
      patient_id: new FormControl()
    })
  }

  function (){

    this.registerForm.controls.password.valueChanges.
    subscribe(
                   x => this.registerForm.controls.cnfpass.updateValueAndValidity()
             )
  }

  isValid(controlName) {

  	return this.registerForm.get(controlName).invalid && this.registerForm.get(controlName).touched;
  }
  

  passValidator(control: AbstractControl){

  	if (control && (control.value !==null || control.value !== undefined)){

  		const cnfpassValue = control.value;

  		const passControl = control.root.get("password");

  		if (passControl){

  			const passValue = passControl.value;

  			if (passValue !==cnfpassValue || passValue ===""){

  				return {isError :true};
  			}
  		}
  	}

  	return null;

  }


