import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import {Subject,Observable} from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from '../../environments/environment';
import jwt_decode from 'jwt-decode';
import {User} from '../models/User';

const headers = {
      'Content-Type': 'application/json',
      'Authorization': localStorage.getItem("token")
    }

@Injectable({
  providedIn: 'root'
})
export class AuthService {

	private currentUserSubject = new Subject <any[]>();
	private  currentUser = this.currentUserSubject.asObservable();

    private currentModuleSubject = new Subject <any[]>();
    private  currentModule = this.currentModuleSubject.asObservable();

    chauser:any;
    moduler:any;


  constructor(private _http: HttpClient) { }


 public currentUserValue():Observable<any[]> {

        return this.currentUser;
 }



 public currentModuleValue():Observable<any[]> {

        return this.currentModule;
    }


setModule(data){


    this.currentModuleSubject.next(data);

    return this.moduler

  }


extractData(data)
  {
        
        // store user details and jwt token in local storage to keep user logged in between page refreshes                
        this.chauser = { "userId": data.authorization.user.user_id,
                         "username": data.authorization.user.username,
                         "expiry_time": data.authorization.expiry_time }

        console.log('decoded-CHECK here',this.chauser);
        localStorage.setItem('currentUser',JSON.stringify(this.chauser));
        localStorage.setItem('token',data.authorization.token);
        this.currentUserSubject.next(this.chauser);
        return this.chauser;
    }

submitLogin(body: any){

     return this._http.post(environment.backend_base_url +"/auth/login",body,{

        observe: "body"
     }).pipe(map(user => this.extractData(user)));
  }

  
  submitRegister(body: any){

  	 return this._http.post(environment.backend_base_url + "/users",body,{

  	 	observe: "body",
        headers:{'Content-Type': 'application/json',
                 'Authorization': localStorage.getItem("token")}
      
  	 })
  }  

  getDecodedAccessToken(token: string): any {
    try{
        return jwt_decode(token);
    }
    catch(Error){
        return null;
    }
  }
 
  addGlobalProperty(body: any){

     return this._http.post(environment.backend_base_url +"/global_properties",body,{
      
      observe: "body",
     headers:{  'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")}     
     })
  }

  getGlobalProperty(body: any){

     return this._http.get(environment.backend_base_url +"/global_properties",{
      
        observe: "body",
        headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")},
        params: body,    
     })
  }

   submitPerson(body: any){

  	 return this._http.post(environment.backend_base_url +"/people",body,{

  	 	  observe: "body",
          headers:{'Content-Type': 'application/json',
                   'Authorization': localStorage.getItem("token")}
  	 })
  }

  changePerson(body: any){

     return this._http.patch(environment.backend_base_url +"/people/"+body.person_id,body,{

      observe: "body",
      headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")}

     })
  }

  submitPatient(body: any){

     return this._http.post(environment.backend_base_url +"/patients",body,{

        observe: "body",
        headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")}
     })
  }

    addPatientIdentifier(body: any){

     return this._http.post(environment.backend_base_url +"/patient_identifiers",body,{

        observe: "body",
        headers: {'Content-Type': 'application/json',
                  'Authorization': localStorage.getItem("token")}
     })
  }

   saveEncounter(body: any){

     return this._http.post(environment.backend_base_url +"/encounters",body,{

        observe: "body",
        headers:{'Content-Type': 'application/json',
                 'Authorization': localStorage.getItem("token")}
     })
  }

    saveObs(body: any){

     return this._http.post(environment.backend_base_url +"/observations",body,{

        observe: "body",
        headers:{'Content-Type': 'application/json',
                 'Authorization': localStorage.getItem("token")}
     })
  }

  enrollPatient(body: any){

     let patient_id = body.person_id;

     return this._http.post(environment.backend_base_url +"/patients/"+patient_id+"/programs",body,{

        observe: "body",
        headers:{'Content-Type': 'application/json',
                 'Authorization': localStorage.getItem("token")}
     })
  }



  updateObs(body: any){

     return this._http.patch(environment.backend_base_url +"/observations/"+body.id,body,{

      observe: "body",
      headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")},
     })
  }


  getUserid(){

  	 return this._http.get(environment.backend_base_url +"/userid",{

  	 	observe: "body",

  	 	params: new HttpParams().append("token",localStorage.getItem("token"))
  	 })
  }


    getRoles(){

     return this._http.get(environment.backend_base_url +"/roles",{

      observe: "body",
      headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")}

     })
  }

    getRegimens(body: any){

     return this._http.get(environment.backend_base_url +"/programs/1/regimen_moh",{

      observe: "body",
      headers:body,

         //params: new HttpParams().append("regimens","moh_regimens")
     })
  }


   getPrisons(body: any){

     return this._http.get(environment.backend_base_url +"/locations",{

      observe: "body",
      headers:  body,

        params: new HttpParams().append("name","Prison")
     })
  }

    
  getPrisoners(body: any){

     return this._http.get(environment.backend_base_url +"/patients",{

      observe:"body",
      headers:{'Content-Type': 'application/json',
               'Authorization': localStorage.getItem("token")},
      params: new HttpParams().append("page",body.page_number)

    })

  }


  getProgrampatients(body: any){

     return this._http.get(environment.backend_base_url +"/patient_programs",{

      observe:"body",
      headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")},
      params: new HttpParams().append("page",body.page_number)

    })

  }

   

  getPeople(){

     return this._http.get(environment.backend_base_url +"/people",{

      observe:"body",
      headers: {'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")}
      //params: new HttpParams().append("page","1")

    })

  }
  getPerson(body:any){

     return this._http.get(environment.backend_base_url +"/people/"+body,{

      observe:"body",
      headers: {'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")}
    })

  }

   getPrisonerEncounters(body: any){

     return this._http.get(environment.backend_base_url +"/encounters",{

      observe:"body",
      headers: {'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")},
      params:body,
      //params: new HttpParams().append("patient_id",body.patient_id,"page",body.page_count)

    })

  }

  patientSearch(body: any){

     return this._http.get(environment.backend_base_url +"/search/patients/by_identifier",{

      observe: "body",
      headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")},
      params: body,
      
     })
  }

  patientIdentity(body: any){

     return this._http.get(environment.backend_base_url +"/patient_identifiers",{

      observe: "body",
      headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")},
      params: body,
      
     })
  }

  getPrograms(body: any){

     return this._http.get(environment.backend_base_url +"/patients/"+body+"/programs",{

      observe: "body",
      headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")}
     
     })
  }

  getWorkflows(body: any){

     return this._http.get(environment.backend_base_url +"/programs/"+body+"/workflows",{

      observe: "body",
      headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")},
     
     })
  }

  changePatientState(body: any){

       return this._http.post(environment.backend_base_url +"/programs/"+body.program_id+"/patients/"+body.patient_id+"/states",body,{

       observe: "body",
       headers:{'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")}
          
     })
  }


   getVisits(body: any) {

     return this._http.get(environment.backend_base_url +"/patients/"+body.patient_id+"/visits",{

      observe:"body",
      headers: {'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")}
    })

  }

  getEncounterObs(body: any){

     return this._http.get(environment.backend_base_url +"/encounters/"+body.encounter_id+"/observations",{

      observe:"body",
      headers: {'Content-Type': 'application/json',
                'Authorization': localStorage.getItem("token")},
      params:{"page_size":"20"}

    })

  }


}

